.table11-div {
  padding-top: 10px;
  padding-bottom: 20px;
}

.table1 {
  border-collapse: separate;
  border-spacing: 0;
  min-width: 600px;
  width: auto;
  margin: 0 auto;
  color: white;
}

.table1 tr th,
.table1 tr td {
  border-right: 1px solid #242222;
  border-bottom: 1px solid #242222;
  padding: 5px;
  font-weight: bold;
  background: white;
  color: black;
}

.table1 tr th:first-child,
.table1 tr td:first-child {
  border-left: 1px solid #242222;
}

.table1 tr th {
  background: #D3D3D3;
  border-top: 1px solid #242222;
  text-align: center;
}

.table1 tr:first-child th:first-child {
  border-top-left-radius: 6px;
}

.table1 tr:first-child th:last-child {
  border-top-right-radius: 6px;
}

.table1 tr:last-child td:first-child {
  border-bottom-left-radius: 6px;
}

.table1 tr:last-child td:last-child {
  border-bottom-right-radius: 6px;
}


